body {
  background: #99b7ff;
  color: #000029; }

body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif; }

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0; }

main {
  display: inline-block;
  text-align: center; }

code {
  font-family: monospace;
  color: #9c3434;
  page-break-inside: avoid;
  font-size: 14px;
  max-width: 100%;
  overflow: auto;
  padding: 0.2em 0.3em;
  word-wrap: break-word;
  background: #fefbee;
  border-radius: 15px;
  margin: 0 3px; }
